import React, { Component } from 'react';
import CardList from './CardList';
import SearchBox from './SearchBox';
import { jedis } from './jedis';
import './App.css';


class App extends Component {

    constructor() {
        super()
        this.state = {
            jedis: jedis,
            searchfield: ''
        }
    }


    onSearchChange = (event) => {
        this.setState({ searchfield: event.target.value })
       }


    render() {
        const filteredJedi = this.state.jedis.filter(jedi => {
            return jedi.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
        })
        return (
            <div className='tc'>
                <h1 className='f1'>Force is strong with this one</h1>
                <SearchBox searchChange={this.onSearchChange}/>
                <CardList jedis= {filteredJedi}/>
            </div>
        );
    }
    
}

export default App;