import React from 'react';

const Card = ({name, species, id}) => {
    return (

        <div className='white tc bg-blue dib br3 pa3 ma2 grow bw2 shadow-3'>
            <img alt ='Jedi Pic' src={require(`./starz/${id}.png`)} />

            <div>
                <h2>{name}</h2>
                <p>{species}</p>
            </div>

        </div>
    )
}


export default Card;