import React from 'react';
import Card from './Card';


const CardList = ({ jedis }) => {
    
    return (
        <div> 
        {
            jedis.map((user, i) => {
                return (
                    <Card 
                key={i} 
                id={jedis[i].id} 
                name={jedis[i].name} 
                species={jedis[i].species} 
                    />
                );           
            })
        }
        </div>
    );
}

export default CardList;